import { useCallback } from "react"
import { useDispatch } from 'react-redux';
import { scheduleScreenChange } from "../../reducers/actions/game";
import TitledLayout from "../TitledLayout";
import logoSrc from "../../assets/logo_2line.png"

import LargeButton from "../LargeButton";

export default function Menu() {
  const dispatch = useDispatch();
  
  const changeScreen = useCallback((screen) => {
    dispatch(scheduleScreenChange(screen));
  }, [dispatch]);

  const [
    gotoPlay, 
    gotoSettings,
    gotoCredits,
  ] = [
    useCallback(() => changeScreen('play'), [changeScreen]),
    useCallback(() => changeScreen('settings'), [changeScreen]),
    useCallback(() => changeScreen('credits'), [changeScreen]),
  ];

  return <TitledLayout className="menu-screen" title={<img className="logo" src={logoSrc} alt="Limited Space game logo"></img>}>
    <div className="main-menu-buttons">
      <LargeButton onClick={gotoPlay}>Play</LargeButton>
      <LargeButton onClick={gotoSettings}>Settings</LargeButton>
      <LargeButton onClick={gotoCredits}>Credits</LargeButton>
    </div>
  </TitledLayout>;
}
